// HTML    
// const locationsData = JSON.parse(
// document.getElementById('map').dataset.locations,
// );
// const locationsData = JSON.parse(
//   document.getElementById('map').getAttribute('data-locations'),
// );
export const displayMap = (locationsData) => {
  const mapInfo = document.getElementById('map').dataset.info;
  console.log(locationsData, mapInfo);

  //       
  const mapElement = document.getElementById('map');
  if (mapElement) {
    // Leaflet   
    const map = L.map('map', {
      center: [37.5, -113.5], //   
      zoom: 5, //    
      // scrollWheelZoom: false, //     
      // zoomControl: false, //    
    }).setView([37.5, -113.5], 5); //      

    // // (  ) OpenStreetMap  
    // L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    //   attribution:
    //     '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
    // }).addTo(map);

    //      (CartoDB Light)
    L.tileLayer('https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}.png', {
      attribution:
        '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
    }).addTo(map);

    // //     (CartoDB Dark)
    // L.tileLayer('https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}.png', {
    //   attribution:
    //     '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
    // }).addTo(map);

    //      
    locationsData.forEach((location) => {
      const { coordinates, description, day } = location;

      // //    L.divIcon
      // const customIcon = L.divIcon({
      //   className: 'custom-icon', //       CSS
      //   html: `<div style="background-color: red; width: 30px; height: 30px; border-radius: 50%; border: 2px solid white;"></div>`, //   
      //   iconSize: [30, 30], //   
      // });

      const customIcon = L.icon({
        // iconUrl: '/img/mapIcon.png',
        iconUrl: '/img/pin.png', //     PNG
        iconSize: [32, 32], //   
        iconAnchor: [16, 32], //      
        popupAnchor: [0, -32], //        
      });

      //        
      L.marker([coordinates[1], coordinates[0]], { icon: customIcon }) // [latitude, longitude]
        .addTo(map)
        .bindPopup(
          `<h3 class="popup-title">${description}</h3><p class="popup-day">Day: ${day}</p>`,
        );
    });

    //        
    const bounds = new L.LatLngBounds();
    locationsData.forEach((location) => {
      bounds.extend([location.coordinates[1], location.coordinates[0]]);
    });

    //       fitBounds   padding
    map.fitBounds(bounds, {
      padding: [50, 50], //  padding    
    });

    // //      
    const latLngs = locationsData.map((location) => [
      location.coordinates[1],
      location.coordinates[0],
    ]);
    L.polyline(latLngs, {
      // color: '#a9a9a9', // dark gray
      color: '#55c57a', // base green color
      opacity: 0.7,
      weight: 3, //  
      opacity: 0.7, //  
      dashArray: '5, 10',
    }).addTo(map);
  }
};
